.terms {
  .terms-cont {
    padding: 0 5.8vw;
    font-size: 3.4vw;
    color: #898989;
    line-height: 4.8vw;
    & > ul > li {
      margin-bottom: 6.5vw;
    }
    strong {
      display: block;
      margin-bottom: 2.5vw;
      color: #1f1f1f;
    }
    .num-list {
      margin-left: 4vw;
      & > li {
        margin-bottom: 2vw;
        list-style-type: decimal;
      }
    }
    .list-dot-style {
      margin-left: .25vw;
      & > li {
        font-size: 3.4vw;
        color: #898989;
        margin-top: 0;
      }
    }
    .list-dash-style {
      position: relative;
      display: inline-block;
      margin-left: 3vw;
      &::before {
        content: '';
        position: absolute;
        top: 2.4vw;
        left: -2vw;
        border-top: .25vw solid #898989;
        width: 1vw;
      }
    }
    .table-help {
      width: 100%;
      text-align: center;
      font-size: 2.7vw;
      color: #1f1f1f;
      border-collapse: collapse;
      th {
        font-weight: bold;
        background-color: #d9d9d9;
      }
      th,
      td {
        border: 1px solid #1f1f1f;
        vertical-align: middle;
      }
    }
    .depth1 {
      padding-left: 3.466vw;
      margin-bottom: 4vw;
    }
    .depth2 {
      padding-left: 1.066vw;
    }
    .depth3 {
      padding-left: 3.333vw;
    }
    .decimal-list-1 {
      counter-reset: decimal-1;
      & > li {
        position: relative;
        padding-left: 2.666vw;
        text-indent: -1.333vw;
        &::before {
          position: absolute;
          left: -5.866vw;
          counter-increment: decimal-1;
          content: "(" counter(decimal-1) ")";
          text-align: right;
          width: 6.4vw;
        }
      }
      .except {
        text-indent: 0;
        padding-left: 1.333vw;
        li {
          padding-left: 0.666vw;
          text-indent: 1.333vw;
        }
      }
    }
    .decimal-list-2 {
      counter-reset: decimal-2;
      &>li {
        position: relative;
        padding-left: 0.533vw;
        text-indent: 0;
        &::before {
          position: absolute;
          left: -3.066vw;
          counter-increment: decimal-2;
          content: counter(decimal-2) ")";
        }
      }
    }
    .dash-list {
      &>li {
        position: relative;
        padding-left: 0.533vw;
        text-indent: 0;
        &::before {
          position: absolute;
          left: -2vw;
          content: "-";
        }
      }
    }
    &.privacy {
      margin-top: 4.8vw;
      & > ul > li > p {
        margin-bottom: 1.2vw;
      }
    }
  }
}

@media (min-width: 750px) {
  .terms {
    .terms-cont {
      padding: 0 43px;
      font-size: 25px;
      color: #898989;
      line-height: 36px;
      & > ul > li {
        margin-bottom: 49px;
      }
      strong {
        display: block;
        margin-bottom: 19px;
        color: #1f1f1f;
      }
      .num-list {
        margin-left: 30px;
        & > li {
          margin-bottom: 15px;
          list-style-type: decimal;
        }
      }
      .list-dot-style {
        margin-left: 2px;
        & > li {
          font-size: 26px;
          color: #898989;
          margin-top: 0;
        }
      }
      .list-dash-style {
        margin-left: 23px;
        &::before {
          top: 19px;
          left: -15px;
          border-top: 2px solid #898989;
          width: 8px;
        }
      }
      .table-help {
        font-size: 20px;
      }
      .depth1 {
        padding-left: 26px;
        margin-bottom: 30px;
      }
      .depth2 {
        padding-left: 8px;
      }
      .depth3 {
        padding-left: 25px;
      }
      .decimal-list-1 {
        & > li {
          padding-left: 20px;
          text-indent: -10px;
          &::before {
            left: -44px;
            width: 48px;
          }
        }
        .except {
          padding-left: 10px;
          li {
            padding-left: 5px;
            text-indent: 10px;
          }
        }
      }
      .decimal-list-2 {
        &>li {
          padding-left: 4px;
          &::before {
            left: -23px;
          }
        }
      }
      .dash-list {
        &>li {
          padding-left: 4px;
          &::before {
            left: -15px;
          }
        }
      }
      &.privacy {
        margin-top: 36px;
        & > ul > li > p {
          margin-bottom: 9px;
        }
      }
    }
  }
}
